@import url(https://fonts.googleapis.com/css2?family=Playfair+Display&display=swap);

body{margin:0;font-family:'Playfair Display', serif}body .header{margin:1%}body .main{margin:2%}body .footer{color:white;margin:2vh auto}body .footer a{color:white}

.MuiPaper-root{margin-bottom:2%}.MuiPaper-root .MuiTab-root{color:white}

.MuiFormControl-root{width:10%}

.chart-container{width:100%}.chart-container .canvas{margin:10px}

.data-cell{display:flex;flex-direction:column;justify-content:center;align-items:center;background-color:white;width:90%;margin:1% auto auto auto}.data-cell p,.data-cell h2{margin:1%}.data-cell .current-number{font-size:3rem;color:#638a72}.data-cell h2{font-size:2rem}.data-cell .increased{font-size:1rem}.odometer.odometer-auto-theme,.odometer.odometer-theme-default{display:inline-block;vertical-align:middle;*vertical-align:auto;*zoom:1;*display:inline;position:relative}.odometer.odometer-auto-theme .odometer-digit,.odometer.odometer-theme-default .odometer-digit{display:inline-block;vertical-align:middle;*vertical-align:auto;*zoom:1;*display:inline;position:relative}.odometer.odometer-auto-theme .odometer-digit .odometer-digit-spacer,.odometer.odometer-theme-default .odometer-digit .odometer-digit-spacer{display:inline-block;vertical-align:middle;*vertical-align:auto;*zoom:1;*display:inline;visibility:hidden}.odometer.odometer-auto-theme .odometer-digit .odometer-digit-inner,.odometer.odometer-theme-default .odometer-digit .odometer-digit-inner{text-align:left;display:block;position:absolute;top:0;left:0;right:0;bottom:0;overflow:hidden}.odometer.odometer-auto-theme .odometer-digit .odometer-ribbon,.odometer.odometer-theme-default .odometer-digit .odometer-ribbon{display:block}.odometer.odometer-auto-theme .odometer-digit .odometer-ribbon-inner,.odometer.odometer-theme-default .odometer-digit .odometer-ribbon-inner{display:block;-webkit-backface-visibility:hidden}.odometer.odometer-auto-theme .odometer-digit .odometer-value,.odometer.odometer-theme-default .odometer-digit .odometer-value{display:block;-webkit-transform:translateZ(0)}.odometer.odometer-auto-theme .odometer-digit .odometer-value.odometer-last-value,.odometer.odometer-theme-default .odometer-digit .odometer-value.odometer-last-value{position:absolute}.odometer.odometer-auto-theme.odometer-animating-up .odometer-ribbon-inner,.odometer.odometer-theme-default.odometer-animating-up .odometer-ribbon-inner{transition:transform 1.5s}.odometer.odometer-auto-theme.odometer-animating-up.odometer-animating .odometer-ribbon-inner,.odometer.odometer-theme-default.odometer-animating-up.odometer-animating .odometer-ribbon-inner{transform:translateY(-100%)}.odometer.odometer-auto-theme.odometer-animating-down .odometer-ribbon-inner,.odometer.odometer-theme-default.odometer-animating-down .odometer-ribbon-inner{transform:translateY(-100%)}.odometer.odometer-auto-theme.odometer-animating-down.odometer-animating .odometer-ribbon-inner,.odometer.odometer-theme-default.odometer-animating-down.odometer-animating .odometer-ribbon-inner{transition:transform 1.5s;transform:translateY(0)}.odometer.odometer-auto-theme,.odometer.odometer-theme-default{font-family:"Helvetica Neue", sans-serif;line-height:1.1em}.odometer.odometer-auto-theme .odometer-value,.odometer.odometer-theme-default .odometer-value{text-align:center}tbody tr:nth-of-type(even){background-color:#638a72}

.stat-display{display:grid;grid-template-columns:repeat(2, 1fr);width:80%;background-color:#AEF3CA;min-width:400px;padding:1% 0;margin:auto}@media screen and (max-width: 1000px){.stat-display{grid-template-columns:repeat(1, 1fr)}}

.choro{width:80%}.choro g path{transition:all .5s}.choro g path:hover{fill:#210146;cursor:pointer}

